.logo{
    width: 10rem;
    max-height: 4rem !important;
    height: 10rem !important;

}
h1{
    text-align: center;
    font-weight: bold;
    font-size: 3rem;
}

/***********Theme Dark/Light*****************/
.dark-theme-btn{
    position: fixed;
    z-index: 999;
    top: 0;
    right: 20vw;
    width: 4rem;
    height: 4rem;
    border-radius: 50%;
    font-size: 2rem;
    font-weight: bold;
    background-color: transparent;
    color: #000;
    cursor: pointer;
    outline: 0;
    border: 0;
    transition: all 0.3s ease-out;
}

.dark-theme-btn:hover{
    background-color: transparent;
    color: yellow;
}

.dark-mode{
    background-color: #000 !important;
    color: #FFF;
}